.popup__input {
    margin: 0;
    padding: 0;
    max-width: 358px;
    height: 27px;
    margin-bottom: 5px;
    font-size: 14px;
    line-height: 17px;
    border-style: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.popup__input:focus:invalid {
    border-bottom: 1px solid rgba(255, 0, 0, 1);
}



@media screen and (max-width: 500px) {
    .popup__input {
        width: 100%;
        margin: auto;
        margin-bottom: 6px;
    }

   
    
}
